header {
  @include linear-gradient(#564a88, #445a95);
  z-index: 9999;
  height: 50px;
  width: 100%;
  position: fixed;
  top: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #2a385f;
  -webkit-box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);

  .row {
    @include max-width();
    width: 100%;
    height: 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    img.pav-icon {
      height: 30px;
    }

    section.search {
      display: flex;
      justify-content: center;
      flex-direction: column;
      max-width: 400px;
      width: 100%;
      height: 30px;
      position: relative;

      input {
        width: 100%;
        height: 26px;
        border-radius: 5px;
        border: none;
        outline: none;
        background-color: white;
        color: $black;
        @include font-size(12px);
        padding-left: 10px;
      }

      img {
        position: absolute;
        top: 6px;
        right: 10px;
        height: 16px;
      }
    }

    section.notification {
      img {
        height: 30px;
      }

      img.user-img {
        height: 30px;
        border-radius: 5px;
        padding-left: 10px;
      }
    }
  }
}

.feed {
  padding-top: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .feed-body {
    width: 100%;
    @include max-width();
  }

  .feed-wrapper {
    display: flex;
    flex-direction: row;
    width: 100%;

    .feed-column {
      width: 60%;

      .bill-container {
      	display: flex;
      	flex-direction: column;
      	margin-top: 20px;
     	margin-right: 20px;
     	@include shadow();
        .bill {
          @include bill();
          h3 {
          	@include font-size(14px);
          	font-family: $font-bold;
          	color: $blue;
          }
          hr {
          	border: none;
          	border-bottom: 1px solid $widgetborder;
          }
        }
        .buttons {
        	display: flex;
        	flex-direction: row;
        	width: 100%;
        	button {
        	cursor: pointer;
        	background-color: #f4f2fe;
        	color: $black;
        	height: 35px;
        	@include font-size(12px);
        	border: none;
        	width: 33.333%;
 			border-radius: 0;
 			transition: all 0.3s ease;
	 			&:nth-child(1){
	 				border: 1px solid $widgetborder;
	 				border-top: none;
	 				border-bottom-left-radius: 3px;		
	 			}	
	 			&:nth-child(2){
	 				border-top: none;
	 				border-bottom: 1px solid $widgetborder;
	 			}
	 			&:nth-child(3) {
	 				border: 1px solid $widgetborder;
	 				border-top: none;
	 				border-bottom-right-radius: 3px;
	 			}
	 			&:hover {
	 				background-color: darken(#f4f2fe, 5%);
	 			}
        	}
        }
      }
    }

    .trending {
      width: 40%;
    }
  }
}

.welcome-message {
  @include box();
  padding: 40px;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: row;

  img {
    display: flex;
    height: 100px;
    justify-content: center;
  }

  img.close {
    position: absolute;
    top: 20px;
    right: 20px;
    height: 15px;
  }

  section {
    padding-left: 40px;

    h1 {
      @include font-size(32px);
      font-family: $font-bold;
      color: $black;
      padding-bottom: 20px;
    }

    p {
      @include font-size(14px);
      font-family: $font-normal;
      color: $black;
      line-height: 24px;
    }
  }
}
